export default {
  props: {
    // ID
    id: String,
    // 
    visible: {
      type: Boolean,
      default: true
    },
    // 
    title: String,
    // 
    index: Number,
  },
  data () {
    return {
      // layer
      layer: null,
      type: 'layer',
      // 
      defaultIndex: 0,
      // 
      createCallback: null,
      // 
      viewContainer: null
    }
  },
  watch: {
    /**
     * 
     * @param _new
     * @param old
     */
    visible: function (_new, old) {
      if (_new !== old) {
        if (this.layer !== null) {
          this.layer.visible = _new
        } else if (_new === true) {
          this.createGisLayer()
        }
      }
    }
  },
  methods: {
    // 
    init (viewContainer, index, callback) {
      this.defaultIndex = index
      this.viewContainer = viewContainer
      this.createCallback = callback
      // 
      if (this.visible === true) {
        this.createGisLayer()
      }
    },
    createGisLayer () {
      /* eslint-disable */
      console.warn("")
    },
    /**
     * 
     */
    getVisible () {
      return this.visible
    },
    /**
     * 
     */
    getIndex: function () {
      if (this.index) {
        return this.index
      } else {
        return this.defaultIndex
      }
    },
    /**
     * 
     */
    getGisMap () {
      if (this.viewContainer === null) {
        return null
      } else {
        return this.viewContainer.activeView.map
      }
    }
  }
}
